import { Injectable } from "@angular/core";
import { BehaviorSubject } from "rxjs";

// deep copy object;
function copy(source) {
  return {
    to: function(target) {
      if (!target || target.constructor != Object) {
        target = {};
      }
      if (source != null && source.constructor == Object) {
        for (let i in source) {
          if (source[i] != null) {
            if (source[i].constructor == Object) {
              copy(source[i]).to(target[i]);
            } else if (source[i].constructor == Array) {
              target[i] = [].concat(source[i]);
            } else {
              target[i] = source[i];
            }
          } else {
            target[i] = null;
          }
        }
      }
      return target;
    }
  };
}

// store the state of application;
const miniState = {
  dashboard: {},
  login: {},
  "step-1": {},
  "step-2": {},
  "step-3": {},
  "step-4": {},
  "step-5": {},
  "step-6": {},
  "step-7": {}
};

@Injectable({
  providedIn: "root"
})
export class GlobalService {
  private appState = new BehaviorSubject("open");
  rootState = this.appState.asObservable();

  constructor() {}

  getState(pageName, stateName) {
    var microState = copy(miniState[pageName] || {}).to({});
    if (!stateName) {
      return microState;
    } else {
      return microState[stateName];
    }
  }

  setState(pageName, updateData) {
    // init page state;
    if (miniState[pageName] == null) {
      miniState[pageName] = {};
    }
    copy(updateData).to(miniState[pageName]);
    this.appState.next(pageName);
  }
}
