import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { AuthGuard } from "../../services/auth-guard.service";
import { GlobalService } from "../../services/global.service";

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.scss"]
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  showAddAlertModal = false;
  submitted = false;

  constructor(
    private fb: FormBuilder,
    private authGuard: AuthGuard,
    private router: Router,
    private rootService: GlobalService
  ) {
    this.createForm();
  }

  ngOnInit() {}

  createForm() {
    this.loginForm = this.fb.group({
      studio: ["", Validators.required],
      username: ["a@b.com", [Validators.required, Validators.email]],
      password: ["1", Validators.required]
    });
  }

  get f() {
    return this.loginForm.controls;
  }

  onSubmit() {
    const formValue = this.loginForm.value;
    this.submitted = true;
    if (this.loginForm.invalid) {
      return;
    }
    this.rootService.setState("login", {
      studio: formValue.studio
    });
    this.router.navigate(["main"]);

    // this.authGuard
    //   .authenticate(formValue.username, formValue.password)
    //   .subscribe(
    //     response => {
    //       if (response["message"]) {
    //         this.showAddAlertModal = true;
    //       } else {
    //         sessionStorage.setItem("token", response.toString());
    //         sessionStorage.setItem("studio", formValue.studio);
    //         sessionStorage.setItem("username", formValue.username);
    //         this.router.navigate(["main"]);
    //       }
    //     },
    //     error => {
    //       this.showAddAlertModal = true;
    //     }
    //   );
  }
  onOkay() {
    this.showAddAlertModal = false;
  }
}
