import { RouterModule, Routes } from "@angular/router";
import { NgModule } from "@angular/core";

import { AuthGuard } from "../services/auth-guard.service";

import { MainComponent } from "../main.component";
import { DashboardComponent } from "../screens/dashboard/dashboard.component";
import { ClientDetailComponent } from "../screens/client-detail/client-detail.component";
import { AddNewClientComponent } from "../screens/add-new-client/add-new-client.component";
import { EditClientComponent } from "../screens/edit-client/edit-client.component";
import { Step1Component } from "../screens/steps/step-1/step-1.component";
import { Step2Component } from "../screens/steps/step-2/step-2.component";
import { Step3Component } from "../screens/steps/step-3/step-3.component";
import { Step4Component } from "../screens/steps/step-4/step-4.component";
import { Step5Component } from "../screens/steps/step-5/step-5.component";
import { Step6Component } from "../screens/steps/step-6/step-6.component";
import { Step7Component } from "../screens/steps/step-7/step-7.component";

const mainRouter: Routes = [
  {
    path: "main",
    canActivate: [AuthGuard],
    component: MainComponent,
    children: [
      { path: "", redirectTo: "dashboard", pathMatch: "full" },
      { path: "dashboard", component: DashboardComponent },
      { path: "client-detail/:id", component: ClientDetailComponent },
      { path: "add-new-client", component: AddNewClientComponent },
      { path: "edit-client/:id", component: EditClientComponent },
      { path: "step-1", component: Step1Component },
      { path: "step-2", component: Step2Component },
      { path: "step-3", component: Step3Component },
      { path: "step-4", component: Step4Component },
      { path: "step-5", component: Step5Component },
      { path: "step-6", component: Step6Component },
      { path: "step-7", component: Step7Component }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(mainRouter, { useHash: true })],
  providers: [AuthGuard],
  exports: [RouterModule],
  declarations: []
})
export class MainRouterModule {}
