import {
  Component,
  OnInit,
  ViewChild,
  Output,
  ElementRef,
  EventEmitter
} from "@angular/core";

@Component({
  selector: "app-range-slider",
  templateUrl: "./range-slider.component.html",
  styleUrls: ["./range-slider.component.scss"]
})
export class RangeSliderComponent implements OnInit {
  @ViewChild("range") rangeCover: ElementRef;
  @Output() change = new EventEmitter();

  startX = 0;

  left = 0;

  posX = {
    left: "0px"
  };

  delta = 10;

  isTouch = false;

  rangeStep = 1;

  rangeWidth = 0;

  constructor() {
    this.mouseUp = this.mouseUp.bind(this);
    this.mouseMove = this.mouseMove.bind(this);
  }

  ngOnInit() {}

  ngAfterViewInit() {
    let rangeCover = this.rangeCover.nativeElement;
    let rangeLimit = rangeCover.clientWidth - this.delta;
    this.rangeWidth = rangeLimit;
    this.rangeStep = rangeLimit / 9;
  }

  addEvent(element, evt) {
    for (let o in evt) {
      if (evt.hasOwnProperty(o)) {
        if (element.addEventListener) {
          element.addEventListener(o, evt[o], false);
        } else if (element.attachEvent) {
          element.attachEvent("on" + o, evt[o]);
        } else {
          element["on" + o] = evt[o];
        }
      }
    }
  }

  removeEvent(element, evt) {
    for (let o in evt) {
      if (evt.hasOwnProperty(o)) {
        if (element.removeEventListener) {
          element.removeEventListener(o, evt[o]);
        } else if (element.detachEvent) {
          element.detachEvent("on" + o, evt[o]);
        } else {
          element["on" + o] = null;
        }
      }
    }
  }

  initEvent(evt) {
    evt.stopPropagation();
    if (evt.touches && evt.touches.length) {
      this.isTouch = true;
      evt = evt.changedTouches[0];
    }

    let ele = evt.target;
    if (evt.button != 2 && evt.which != 3) {
      let left = parseInt(ele.style.left) || 0;

      this.startX = evt.clientX;

      this.left = left;

      this.posX = {
        left: left + "px"
      };

      this.addEvent(document, {
        mouseup: this.mouseUp,
        touchend: this.mouseUp
      });

      this.addEvent(document, {
        mousemove: this.mouseMove,
        touchmove: this.mouseMove
      });
    }
  }

  mouseUp(evt) {
    !!this.isTouch && (evt = evt.changedTouches[0]);
    let posX = this.left - (this.startX - evt.clientX);
    let step = Math.round(posX / this.rangeStep);
    posX = step * this.rangeStep;

    this.setPosition(posX);

    // pass value to parent;
    this.change.emit(step + 1);

    this.removeEvent(document, {
      mousemove: this.mouseMove,
      touchmove: this.mouseMove
    });

    this.removeEvent(document, {
      mouseup: this.mouseUp,
      touchend: this.mouseUp
    });
  }

  mouseMove(evt) {
    !!this.isTouch && (evt = evt.changedTouches[0]);
    let posX = this.left - (this.startX - evt.clientX);
    this.setPosition(posX);
  }

  setPosition(posX) {
    if (posX < 0) {
      posX = 0;
    }

    if (posX > this.rangeWidth) {
      posX = this.rangeWidth;
    }

    this.posX = {
      left: posX - this.delta + "px"
    };
  }

  estimateRange(evt) {
    console.log(evt);
    const offsetX = evt.offsetX;
    const step = Math.round(offsetX / this.rangeStep);
    const posX = step * this.rangeStep;
    this.setPosition(posX);
  }
}
