import { BrowserModule } from "@angular/platform-browser";
import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from "@angular/core";
import { ReactiveFormsModule } from "@angular/forms";
import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
import { RouterModule, Routes } from "@angular/router";
import { MatDatepickerModule } from "@angular/material/datepicker";
import { MatNativeDateModule } from "@angular/material/";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { Observable } from "rxjs";

import { HeaderInterceptor } from "./header-interceptor";
import { MainRouterModule } from "./router/main-router.module";
import { UnauthRouterModule } from "./router/unauth-router.module";

import { AppComponent } from "./app.component";
import { MainComponent } from "./main.component";

import { DashboardComponent } from "./screens/dashboard/dashboard.component";
import { LoginComponent } from "./screens/login/login.component";
import { ClientDetailComponent } from "./screens/client-detail/client-detail.component";
import { WelcomeComponent } from "./screens/welcome/welcome.component";
import { AddNewClientComponent } from "./screens/add-new-client/add-new-client.component";
import { EditClientComponent } from "./screens/edit-client/edit-client.component";
import { Step1Component } from "./screens/steps/step-1/step-1.component";
import { Step2Component } from "./screens/steps/step-2/step-2.component";
import { Step3Component } from "./screens/steps/step-3/step-3.component";
import { Step4Component } from "./screens/steps/step-4/step-4.component";
import { Step5Component } from "./screens/steps/step-5/step-5.component";
import { Step6Component } from "./screens/steps/step-6/step-6.component";
import { Step7Component } from "./screens/steps/step-7/step-7.component";
import { AddUserFormComponent } from './components/add-user-form/add-user-form.component';
import { RangeSliderComponent } from './components/range-slider/range-slider.component';

@NgModule({
  imports: [
    BrowserModule,
    HttpClientModule,
    ReactiveFormsModule,

    MainRouterModule,
    UnauthRouterModule,

    BrowserAnimationsModule,

    MatDatepickerModule,
    MatNativeDateModule
  ],
  declarations: [
    AppComponent,
    MainComponent,
    LoginComponent,

    DashboardComponent,

    ClientDetailComponent,

    WelcomeComponent,

    AddNewClientComponent,

    EditClientComponent,

    Step1Component,

    Step2Component,

    Step3Component,

    Step4Component,

    Step5Component,

    Step6Component,

    Step7Component,

    AddUserFormComponent,

    RangeSliderComponent
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS, // add default header;
      useClass: HeaderInterceptor, // add default header;
      multi: true
    }
  ],
  bootstrap: [AppComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class AppModule {}
